.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  width: 100%;
  height: 56px;
  background: $c-white;
  border-radius: 40px;

  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  color: #1d1d1d;
  border: 0;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }
  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }

}
